import React from 'react';
import { useState,useRef } from 'react';
import "./SearchBar.css";

export const SearchBar = () => {
    const searchValue= useRef('');
    const [place, setstate] = useState("Search on this Fake Google");
    async function shubham2(){
        if(searchValue.current.value.toLowerCase()==='porn')
        {
        searchValue.current.value="Shubham Tharki hai";
        setstate(place);
        }

    }
    function shubham(){
        if(place==="Don't Search for porn")
        setstate("Search on this Fake Google")
        else
        setstate("Don't Search for porn");
    }
    return (
        <div className="searchContainer ">
            <i className="fas fa-search searchIconColor"></i>
            <input className="searchInput" type="text" placeholder={place} ref={searchValue} onFocus={shubham} onBlur={shubham} onChange={shubham2}/>
           {searchValue.current.value==="Shubham Tharki hai"?<iframe width="700" height="315" src="https://www.youtube.com/embed/nd4wsdUMorY?autoplay=1" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen />:null}
            <i className="fas fa-microphone micColor"></i>
        </div>
    )
    
}

