import React from 'react'
import { NavLink } from "react-router-dom";

export const Header = () => {
    return (
        <div>
            
            <div className='headerRight'>
            <NavLink to="/home/gmail" activeClassName="selected blue" className="icons">Gmail</NavLink> 
            <NavLink to="/home/images" activeClassName="selected blue" className="icons">Images</NavLink> 
            <NavLink to="/home/aboutus" activeClassName="selected blue" className="icons">About Us</NavLink> 
             
            
            
            <NavLink to="/home/apps" activeClassName="selected blue" ><i className="fas fa-th icons fa-2x"></i></NavLink> 
            <NavLink to="/home/options" activeClassName="selected blue" ><i className="fas fa-user-plus icons fa-2x"></i></NavLink> 
                
                </div>
                
        </div>
    )
}
