import React from 'react';
import { Header } from '../Components/Header';
import './Home.css';
import { Switch, Route, NavLink } from 'react-router-dom';
import { SearchBar } from '../Components/SearchBar';
import googleLogo from '../Images/googleLogo.png'
export const Home = () => {
    const bannerImage = googleLogo;
    return (
        <div>
            <Header />
            {/* Links in header */}
            <Switch>
                <Route exact path='/home'> <h1>This is the home page</h1></Route>
                <Route path='/home/options'><h1>These are the options</h1></Route>
                <Route path='/home/aboutus'><h1>About us page</h1></Route>
                <Route path='/home/apps'><h1>These are the apps</h1></Route>
                <Route path='/home/gmail'><h1>Login to gmail</h1></Route>
                <Route path='/home/images'><h1>Search for Images</h1></Route>
            </Switch>
            <div className='homeBody'>
                <img className="BannerImage" src={bannerImage} alt="Google Logo" />
                {/* Search input bar */}
                <SearchBar />
                <div className="homeButtons">
                    <NavLink to='/home' activeClassName="selected blue" ><button>Google Search</button></NavLink>
                    <NavLink to='/' activeClassName="selected blue" ><button>I'm Feeling Lucky</button></NavLink>

                </div>
            </div>
        </div>
    )
}
