import React from 'react';
import { Home } from './Pages/Home';
import {
  BrowserRouter as Router,
  Switch,
  Route,
} from "react-router-dom";
import { SearchResult } from './Pages/SearchResult';


export function App() {
  console.log('first app');
  return (
    <div>
      <Router>

        <Switch>
          <Route path="/home">
            {/* Home page */}
            <Home />
          </Route>



          <Route path="/search">

            {/* Search page */}
            <SearchResult/>
          </Route>


          <Route>
            {/* Default path*/}
            <Home />
          </Route>

        </Switch>

      </Router>
    </div>
  );

}